/**
 * ==   Components > Toggles
 */
.toggle {
  background: #fff;
  border-radius: 4px;
  border: rgba(60, 60, 60, 0.15) 1px solid;
  display: inline-block;
  height: 1.875rem;
  margin-right: .5rem;
  vertical-align: middle;
  width: 5.25rem;
}

.toggle .toggle-checkbox {
  cursor: pointer;
  margin: 0;
  opacity: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}

.toggle .not-allowed {
  cursor: not-allowed;
}

.toggle .knobs,
.toggle .layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.toggle .knobs {
  z-index: 2;
}

.toggle .layer {
  -webkit-transition: .3s ease all;
  -o-transition: .3s ease all;
  transition: .3s ease all;
  width: 100%;
  z-index: 1;
}

.toggle .knobs:before {
  content: '開啟中';
  position: absolute;
  top: 0.125rem;
  left: 0.125rem;
  width: 3rem;
  height: .75rem;
  background-color: #1c84c6;
  color: #fff;
  line-height: 1;
  padding: .375rem .25rem;
  font-size: 12px;
  border-radius: 2px;
  text-align: center;
  transition: 0.3s ease all, left 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15);
}

.toggle .toggle-checkbox:checked + .knobs:before {
  content: '關閉中';
  left: 1.5rem;
  background-color: #b1b6bc;
}

.toggle .not-finish:checked + .knobs:before {
  content: '未完成';
  left: 1.5rem;
  background: #F5F6FA;
  color: #b1b6bc;
  cursor: not-allowed;
}

.toggle .knobs.orange:before {
  content: '同步中';
  background-color: #FF9A75;
}

.toggle .toggle-checkbox:checked + .knobs.orange:before {
  content: '非同步';
  left: 1.5rem;
  background-color: #b1b6bc;
}

.toggle .not-finish:checked + .knobs.orange:before {
  content: '未完成';
  left: 1.5rem;
  background: #F5F6FA;
  color: #b1b6bc;
  cursor: not-allowed;
}

.fade {
  -webkit-transition: opacity 0.15s linear;
  -o-transition: opacity 0.15s linear;
  transition: opacity 0.15s linear;
}

.fade:not(.show) {
  opacity: 0;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.35s ease;
  -o-transition: height 0.35s ease;
  transition: height 0.35s ease;
}

