.list-palete .palete-card {
  border-radius: 4px;
  display: inline-block;
  vertical-align: middle;
  padding: .5rem;
  width: 16.66667%;
}

@media all and (max-width: 1200px) {
  .list-palete .palete-card {
    width: 20%;
  }
}

@media all and (max-width: 992px) {
  .list-palete .palete-card {
    width: 25%;
  }
}

@media all and (max-width: 768px) {
  .list-palete .palete-card {
    width: 33.33333%;
  }
}

@media all and (max-width: 576px) {
  .list-palete .palete-card {
    width: 50%;
  }
}

.list-palete .palete-card .palete-color,
.list-palete .palete-card .palete-color__square {
  height: 6rem;
}

.list-palete .palete-card .palete-color__square {
  width: 50%;
  float: left;
}

.list-palete .palete-card .palete-color__square:hover {
  width: 100%;
  position: absolute;
}

.list-palete .palete-card .palete-color__lighten {
  border-top-left-radius: 4px;
}

.list-palete .palete-card .palete-color__lighten:hover {
  border-top-right-radius: 4px;
}

.list-palete .palete-card .palete-color__darken {
  float: right;
  border-top-right-radius: 4px;
}

.list-palete .palete-card .palete-color__darken:hover {
  border-top-left-radius: 4px;
}

.list-palete .palete-card .palete-color__content {
  background-color: #fafafa;
  font-family: "Cousine", SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.875rem;
  padding: 0.75rem;
  color: #c6c9ca;
  border-radius: 4px;
  box-shadow: 0 3px 0 0 rgba(226, 228, 231, 0.75);
}

.list-palete .palete-card .palete-color__content .palete-color__name {
  font-size: 0.875rem;
  text-transform: uppercase;
  color: #535e60;
}

.list-palete .palete-card .palete-color__content .palete-color__code {
  font-size: 0.75rem;
  line-height: 1.25;
  text-transform: uppercase;
}

/* Typography */
.gui-block .ty-group:first-of-type {
  margin-bottom: 1rem;
}

.gui-block .ty-group:not(:first-of-type) {
  margin: 1rem 0;
}

.gui-block .ty-group .ty-group-head {
  display: inline-block;
  margin-bottom: .25rem;
}

.gui-block .ty-group .ty-group-head:not(:first-child) {
  margin-top: .75rem;
}

.gui-block .ty-group .ty-group-head:before {
  content: '';
  background: rgba(118, 184, 216, 0.24);
  position: absolute;
  left: 0.25rem;
  bottom: 0.125rem;
  width: calc(100% - 0.5rem);
  height: .75rem;
  transition: 0.35s cubic-bezier(0.25, 0.1, 0, 2.05);
}

.gui-block .ty-group .ty-group-head strong {
  font-size: 120%;
  color: #248dc1;
}

.gui-block .ty-group .ty-group-head big {
  font-weight: 700;
  margin-right: .75rem;
  margin-left: .75rem;
}

.gui-block .ty-group .ty-group-head sub {
  color: #289dd6;
}

.gui-block .ty-group .ty-group-head h1, .gui-block .ty-group .ty-group-head h2, .gui-block .ty-group .ty-group-head h3, .gui-block .ty-group .ty-group-head h4, .gui-block .ty-group .ty-group-head h5, .gui-block .ty-group .ty-group-head h6 {
  letter-spacing: 1px;
}

.gui-block .ty-group .ty-group-head .ty-group-bd {
  padding-left: .5rem;
  color: #535e60;
}

.gui-group {
  margin: 1rem 0 2rem;
}

.gui-group .btn {
  margin-right: .5rem;
  margin-bottom: 1rem;
}

.col-style {
  background-color: #444C67;
  border: 1px solid #fff;
  border-radius: 2px;
  color: #fff;
  font-size: .75rem;
  text-transform: uppercase;
  padding: .75rem .5rem;
  box-shadow: 0 0.25rem 0.5rem rgba(25, 40, 43, 0.04);
}

/*  icon set
----------------------------------------  */
.gui-icon-blk {
  float: left;
  width: 8.33333%;
}

@media all and (max-width: 1200px) {
  .gui-icon-blk {
    width: 10%;
  }
}

@media all and (max-width: 992px) {
  .gui-icon-blk {
    width: 12.5%;
  }
}

@media all and (max-width: 768px) {
  .gui-icon-blk {
    width: 16.66667%;
  }
}

@media all and (max-width: 576px) {
  .gui-icon-blk {
    width: 25%;
  }
}

.gui-icon-blk .gui-icon-cnt {
  background-color: #f3f3f4;
  display: block;
  padding: 1rem .5rem .5rem;
  text-align: center;
  box-shadow: 4px 4px 4px 0 #d1d9e6 inset, -4px -4px 4px 0 #fff inset;
}

.gui-icon-blk .gui-icon-cnt > a {
  display: block;
}

.gui-icon-blk .gui-icon-cnt i {
  font-size: 1.25rem;
}

.gui-icon-blk .gui-icon-cnt p {
  font-family: "Cousine", SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: .75rem;
  color: #535e60;
  background-color: #cacfd6;
  margin-top: 1rem;
  white-space: nowrap;
  overflow-x: auto;
  overflow: -moz-hidden-unscrollable;
}

.gui-icon-blk .gui-icon-cnt p::-webkit-scrollbar {
  display: none;
}
